import React, { useState } from "react";
import About from "../components/about";
import Links from "../components/links";
import Profile from "../components/profile";
import Skills from "../components/skills";

const Home = () => {
  const [showPortfolioContent, setshowPortfolioContent] = useState(false);
  const [showBlogContent, setshowBlogContent] = useState(false);
  const handlePortfolioData = () => {
    const valueTobeSet = !showPortfolioContent;
    setshowPortfolioContent(valueTobeSet);
  };
  const handleBlogData = () => {
    const valueTobeSet = !showBlogContent;
    setshowBlogContent(valueTobeSet);
  };
  return (
    <div>
      <center>
        <br />
        <br />
        <Profile />
        <hr />
        <button onClick={handlePortfolioData}>PortFolio</button>
        <button onClick={handleBlogData}>blog</button>
        {showPortfolioContent ? (
          <>
            <About />
            <Skills />
            <Links />
          </>
        ) : (
          <div></div>
        )}
        {showBlogContent ? (
          <>
            <p>in progress</p>
          </>
        ) : (
          <div></div>
        )}
      </center>
    </div>
  );
};

export default Home;
